            search:function(){
            let lista=[];
            lista= this.dataAudio
            .filter((audio) => audio.codigo.toLowerCase().includes(this.$store.state.searchText.toLowerCase()))
                
                return lista.sort(function(a, b) {
                    if (a.codigo > b.codigo) {
                    return 1;
                    }
                    if (a.codigo < b.codigo) {
                    return -1;
                    }
                    return 0;
                });

            }  
        },
