                .then(res => res.json())
                .then(data =>{
                for (let ind of data.feed.entry){
                    let temp={
                        "codigo" : ind.gsx$code1.$t,
                        "nombre" : ind.gsx$name.$t,
                        "imagen" : ind.gsx$cdimg.$t, 
                        "character" : ind.gsx$character.$t, 
                        "available" : ind.gsx$available.$t, 
                        "date" : ind.gsx$date.$t,
                        "tags" : ind.gsx$tags.$t
                        // "tracks" : [
                        //     { "mp3": ind.gsx$track1mp3.$t,"flac": ind.gsx$track1flac.$t,"wav": ind.gsx$track1wav.$t,"sub":ind.gsx$track1sub.$t},
                        //     { "mp3": ind.gsx$track2mp3.$t,"flac": ind.gsx$track2flac.$t,"wav": ind.gsx$track2wav.$t,"sub":ind.gsx$track2sub.$t},
                        //     { "mp3": ind.gsx$track3mp3.$t,"flac": ind.gsx$track3flac.$t,"wav": ind.gsx$track3wav.$t,"sub":ind.gsx$track3sub.$t},
                        //     { "mp3": ind.gsx$track4mp3.$t,"flac": ind.gsx$track4flac.$t,"wav": ind.gsx$track4wav.$t,"sub":ind.gsx$track4sub.$t},
                        //     { "mp3": ind.gsx$track5mp3.$t,"flac": ind.gsx$track5flac.$t,"wav": ind.gsx$track5wav.$t,"sub":ind.gsx$track5sub.$t},
                        //     { "mp3": ind.gsx$track6mp3.$t,"flac": ind.gsx$track6flac.$t,"wav": ind.gsx$track6wav.$t,"sub":ind.gsx$track6sub.$t},
                        //     { "mp3": ind.gsx$track7mp3.$t,"flac": ind.gsx$track7flac.$t,"wav": ind.gsx$track7wav.$t,"sub":ind.gsx$track7sub.$t},
                        //     { "mp3": ind.gsx$track8mp3.$t,"flac": ind.gsx$track8flac.$t,"wav": ind.gsx$track8wav.$t,"sub":ind.gsx$track8sub.$t},
                        //     { "mp3": ind.gsx$track9mp3.$t,"flac": ind.gsx$track9flac.$t,"wav": ind.gsx$track9wav.$t,"sub":ind.gsx$track9sub.$t},
                        //     { "mp3": ind.gsx$track10mp3.$t,"flac": ind.gsx$track10flac.$t,"wav": ind.gsx$track10wav.$t,"sub":ind.gsx$track10sub.$t},
                        //     { "mp3": ind.gsx$track11mp3.$t,"flac": ind.gsx$track11flac.$t,"wav": ind.gsx$track11wav.$t,"sub":ind.gsx$track11sub.$t},
                        //     { "mp3": ind.gsx$track12mp3.$t,"flac": ind.gsx$track12flac.$t,"wav": ind.gsx$track12wav.$t,"sub":ind.gsx$track12sub.$t},
                        //     { "mp3": ind.gsx$track13mp3.$t,"flac": ind.gsx$track13flac.$t,"wav": ind.gsx$track13wav.$t,"sub":ind.gsx$track13sub.$t},
                        //     { "mp3": ind.gsx$track14mp3.$t,"flac": ind.gsx$track14flac.$t,"wav": ind.gsx$track14wav.$t,"sub":ind.gsx$track14sub.$t},
                        //     { "mp3": ind.gsx$track15mp3.$t,"flac": ind.gsx$track15flac.$t,"wav": ind.gsx$track15wav.$t,"sub":ind.gsx$track15sub.$t},
                        //     { "mp3": ind.gsx$track16mp3.$t,"flac": ind.gsx$track16flac.$t,"wav": ind.gsx$track16wav.$t,"sub":ind.gsx$track16sub.$t},
                        //     { "mp3": ind.gsx$track17mp3.$t,"flac": ind.gsx$track17flac.$t,"wav": ind.gsx$track17wav.$t,"sub":ind.gsx$track17sub.$t},
                        //     { "mp3": ind.gsx$track18mp3.$t,"flac": ind.gsx$track18flac.$t,"wav": ind.gsx$track18wav.$t,"sub":ind.gsx$track18sub.$t},
                        //     { "mp3": ind.gsx$track19mp3.$t,"flac": ind.gsx$track19flac.$t,"wav": ind.gsx$track19wav.$t,"sub":ind.gsx$track19sub.$t},
                        //     { "mp3": ind.gsx$track20mp3.$t,"flac": ind.gsx$track20flac.$t,"wav": ind.gsx$track20wav.$t,"sub":ind.gsx$track20sub.$t}
                        // ]
                    }
                    
                    this.dataAudio.push(temp);
                }
            })
        }  
    }
</script>
<style scoped>
    .Filtro{
        margin-top: 95px;
        margin-bottom: 20px;
        padding: 5px;
        width: 75%;
        max-width: 1200px;
        margin-left: auto;
        margin-right: auto;
    
        color:rgb(33, 37, 41);
    }
    .icono{
        /* font-size: 20px; */
        border: 1px solid rgb(33, 37, 41);
        padding: 4px 5px 4px 5px;
        border-radius: 50%;
    }
    .Selection{
        padding: 4px 8px 4px 8px;
        margin: 0px 8px 0px 8px;
        border-radius: 4px; 
        background-color: rgb(233, 236, 239);
        color: gray;
        border: 0;
        font-size: 16px;
        outline: 0px;
    }
    .resultados{
        
        display: grid;
        grid-template-columns:  repeat(auto-fill, minmax(240px, 1fr));
        text-align: center;
        justify-content: center;
        width: 1440px;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
    
    } 
</style>    